import './css/infoField.css';


/**
 * Single info field for toolbar
 */
export default class InfoField  {
    constructor(title) {

        // create root div
        const rootHtml = document.createElement("div");
        rootHtml.classList.add('toolbar-balance-bar__item__field');
        this.html = rootHtml;


        // create title span element
        const titleHtml = document.createElement('span');
        titleHtml.setAttribute('class', `toolbar-balance-bar__item__title`);
        rootHtml.appendChild(titleHtml);
        this._titleHtml = titleHtml;


        // create value span element
        const valueHtml = document.createElement('span');
        valueHtml.setAttribute('class', `toolbar-balance-bar__item__value`);
        rootHtml.appendChild(valueHtml);
        this._valueHtml = valueHtml;

        // set initial values
        this.setTitle(title);
        this.setValue(0);
    }

    /**
     * set value
     * @param value
     */
    setValue(value) {
        this._valueHtml.innerHTML = value;
    }

    /**
     * set title text
     * @param title
     */
    setTitle(title) {
        this._titleHtml.innerHTML = title;
    }

    /**
     * fade in with animation
     */
    fadeIn(){
        this.html.classList.add('visibilityFadeIn');
        this.html.classList.remove('visibilityFadeOut');
    }

    /**
     * fade out with animation
     */
    fadeOut(){
        this.html.classList.add('visibilityFadeOut');
        this.html.classList.remove('visibilityFadeIn');
    }

    /**
     * show info field
     */
    show(){
        this.html.classList.remove('invisible');
        this.html.classList.add('visible');
    }

    /**
     * hide info field
     */
    hide(){
        this.html.classList.remove('visible');
        this.html.classList.add('invisible');
    }
}
