import InfoField from './InfoField';

// text constants keys that comes from NGTGame
const TOTAL_WINS = 'TOTAL_WINS';
const WIN_UP_TO = 'WIN_UP_TO';
const BALANCE = 'BALANCE';
const TOTAL_COST = 'TOTAL_COST';

let ServiceFacade

import './css/infoBar.css'
export default class BalanceInfoBar  {
    /**
     *
     * @param config
     *
     * available config properties :
     *
     *
     * {
     *     "hasWinUpToPanel" : true,  // does info bar have win up to field or not
     *     "height" : 10              // Info Bar height in % from parent element
     * }
     *
     * @param texts
     * @param facade
     */
    constructor(config = {}, texts = {}, facade = {}) {

        ServiceFacade = facade;

        /**
         * external config
         * @type {{}}
         * @private
         */
        this._config = config;

        /**
         * balance panel
         * @type {InfoField}
         */
        this.balancePanel = new InfoField(BalanceInfoBar.getText(BALANCE, texts));

        /**
         * total win panel
         * @type {InfoField}
         */
        this.totalWinPanel = new InfoField(BalanceInfoBar.getText(TOTAL_WINS, texts));

        /**
         * total cost panel
         * @type {InfoField}
         */
        this.totalCostPanel = new InfoField(BalanceInfoBar.getText(TOTAL_COST, texts));

        // detect in config - do we have win up to panel or not
        const hasWinUpToPanel = this._config && this._config.hasWinUpToPanel;

         /**
         * win up to panel
         * @type {InfoField | null}
         */
        this.winUpToPanel = hasWinUpToPanel ? new InfoField(BalanceInfoBar.getText(WIN_UP_TO, texts)) : null;

        // we keep totalWinPanelCounter backward compatibility
        this.totalWinPanelCounter = this._createCountUp;

        // crate html elements
        this._create();

        // set height comes from config
        if (this._config && this._config.height){
            this.setHeight(this._config.height)
        }

        // show total win or win up to panel
        if (hasWinUpToPanel){
            this.winUpToPanel.show();
            this.totalWinPanel.hide();
        } else {
            this.totalWinPanel.show();
        }
    }

    /**
     * show entire info bar
     */
    show(){
        this._html.classList.remove('invisible');
        this._html.classList.add('visible');
    }

    /**
     * hide entire info bar
     */
    hide(){
        this._html.classList.remove('visible');
        this._html.classList.add('invisible');
    }

    /**
     * attach
     * @param parent_id
     */
    attach(parent_id){
        // const parent =  document.getElementById(parent_id);
        const parent =  document.body;
        parent.appendChild(this._html)
    }

    /**
     * show win with fade
     */
    showWin() {
        if (this.winUpToPanel) {
            this.winUpToPanel.fadeOut();
            this.totalWinPanel.fadeIn();
        }
    }

    /**
     * show win up to with fade
     */
    showWinUpTo() {
        if (this.winUpToPanel) {
            this.totalWinPanel.fadeOut();
            this.winUpToPanel.fadeIn();
        }
    }

    /**
     * update total cost
     * @param value
     */
    updateTotalCost(value){
        this.totalCostPanel.setValue(value);
    }

    /**
     * update total win
     * @param value
     */
    updateTotalWin(value){
        this.totalWinPanel.setValue(value);
    }

    /**
     * update win up to
     * @param value
     */
    updateWinUpTo(value){
        this.winUpToPanel && this.winUpToPanel.setValue(value);
    }

    /**
     * update balance
     * @param value
     */
    updateBalance(value){
        this.balancePanel.setValue(value);
    }

    /**
     * update balance title
     * @param title
     */
    setBalanceTitle(title){
        this.balancePanel.setTitle(title);
    }

    /**
     * set total win title
     * @param title
     */
    setTotalWinTitle(title){
        this.totalWinPanel.setTitle(title);
    }

    /**
     * set win up to title
     * @param title
     */
    setWinUpToTitle(title){
        this.winUpToPanel.setTitle(title);
    }

    /**
     * set total cost title
     * @param title
     */
    setTotalCostTitle(title){
        this.totalCostPanel.setTitle(title);
    }

    /**
     * set height for root div
     * @param height - height in % from parent element
     */
    setHeight(height){
        this._html.style.height = height + '%';
    }

    /**
     * keep for backward compatibility
     */
    updateLayout() {}


    /**
     * outside debugging
     * @param enable
     */
    debug(enable){
        if (enable){
            this._html.classList.add('debug');
        } else {
            this._html.classList.remove('debug');
        }


    }

    /**
     * create html markup with 3 vertical blocks
     * div.toolbar-balance-bar
     *  => div.toolbar-balance-bar__item - X3
     *      insert to each item infoField
     *
     * @private
     */
    _create(){
        this._html = document.createElement("div");
        this._html.setAttribute('id', 'toolbar-balance-bar');

        // create 3 vertical div items
        for (let i = 0; i < 3; i++){
            // create html item and put it to the root html
            const itemHtml = document.createElement("div");
            itemHtml.classList.add('toolbar-balance-bar__item');
            this._html.appendChild(itemHtml);

            // insert balance panel to the left div
            if (i === 0) itemHtml.appendChild(this.balancePanel.html);

            // inset total win and (if exists) win up to panel to the middle div
            if (i === 1) {
                itemHtml.appendChild(this.totalWinPanel.html)
                this.winUpToPanel  && itemHtml.appendChild(this.winUpToPanel.html);
            }

            // insert total cost to the right div
            if (i === 2) itemHtml.appendChild(this.totalCostPanel.html);
        }
    }

    /**
     * create counter empty API to keep backward compatibility
     * @return {{stopCounting: stopCounting, setMaxTime: setMaxTime, startCounting: startCounting, isCounting: isCounting, reset: reset, setCurrentValue(*)}}
     * @private
     */
    _createCountUp() {
        return {
            startCounting: () => {},
            stopCounting: () => {},
            isCounting: () => {},
            setCurrentValue(value) {},
            reset: () => {},
            setMaxTime: () => {}
        };
    }

    /**
     * get text
     * if no text in the keys object - return key value
     * @param key
     * @param texts
     * @return {*}
     */
    static getText(key = "", texts = {}){
        const text  =  texts[key];
        return text ? text : key;
    }

}
